.container_reponse {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
  width: 100%;
  -webkit-box-shadow: inset 0px 0px 30px 10px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: inset 0px 0px 30px 10px rgba(0, 0, 0, 0.25);
  box-shadow: inset 0px 0px 30px 10px rgba(0, 0, 0, 0.25);
}

.reponse {
  color: #f1fff3;
  font-family: "Paytone One";
  font-size: 144px;
}

.description {
  color: #f1fff3;
  text-align: center;
  font-size: 36px;
  margin-bottom: 36px;
}

.buttonNext {
  font-size: 48px;
  padding: 35px 114px;
  font-family: "Montserrat";
  color: #f1fff3;
  background-color: var(--colorFonce);
  border: none;
  cursor: pointer;
}
