.header_container {
  display: flex;
  align-items: center;
  margin-top: 50px;
}

.title_container {
  width: 100%;
  display: flex;
  justify-content: center;
}

.header_title {
  width: 854px;
  text-align: center;
  margin-right: 10%;
  margin-bottom: 59px;
  position: relative;
  font-size: 72px;
}
.header_title::before {
  content: "";
  z-index: -1;
  position: absolute;
  width: 853px;
  height: 12px;
  left: 0;
  bottom: -12px;
  background: linear-gradient(to right, #006f66, #2fb9ae);
}
