.questionPropositionContainer {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  flex-direction: column-reverse;
  background: var(--colorClair);
}

.questionPropositionContainer::before {
  content: "";
  position: absolute;
  z-index: 8;
  left: 0;
  top: 50%;
  width: 100%;
  height: 37px;
  background: var(--colorFonce);
}

.proposition {
  position: absolute;
  text-align: center;
  color: #f1fff3;
  font-size: 96px;
  top: 41%;
  width: 60%;
  z-index: 10;
  font-family: "Paytone One";
  -webkit-box-shadow: 0px 0px 30px 5px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 0px 30px 5px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 0px 30px 5px rgba(0, 0, 0, 0.25);
  background: var(--colorClair);
}

.button {
  font-size: 96px;
  font-family: "Paytone One";
  cursor: pointer;
  color: #f1fff3;
  background: var(--colorClair);
  border: none;
  width: 100%;
  height: 50%;
  -webkit-box-shadow: inset 0px 0px 30px 10px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: inset 0px 0px 30px 10px rgba(0, 0, 0, 0.25);
  box-shadow: inset 0px 0px 30px 10px rgba(0, 0, 0, 0.25);
  transition: 0.3s ease-in;
}

.button:hover {
  background: var(--colorFonce);
}
