@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&family=Paytone+One&display=swap);
:root {
  --colorClair: #006f66;
  --colorFonce: #054a44;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Montserrat", sans-serif;
  color: #101010;
}

button:focus {
  outline: none;
}

.container_score {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-self: flex-start;
  margin-left: 3%;
}

.question_number {
  border: 12px solid var(--colorClair);
  font-size: 96px;
}

.score {
  font-size: 36px;
}

.header_container {
  display: flex;
  align-items: center;
  margin-top: 50px;
}

.title_container {
  width: 100%;
  display: flex;
  justify-content: center;
}

.header_title {
  width: 854px;
  text-align: center;
  margin-right: 10%;
  margin-bottom: 59px;
  position: relative;
  font-size: 72px;
}
.header_title::before {
  content: "";
  z-index: -1;
  position: absolute;
  width: 853px;
  height: 12px;
  left: 0;
  bottom: -12px;
  background: linear-gradient(to right, #006f66, #2fb9ae);
}

.questionPropositionContainer {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  flex-direction: column-reverse;
  background: var(--colorClair);
}

.questionPropositionContainer::before {
  content: "";
  position: absolute;
  z-index: 8;
  left: 0;
  top: 50%;
  width: 100%;
  height: 37px;
  background: var(--colorFonce);
}

.proposition {
  position: absolute;
  text-align: center;
  color: #f1fff3;
  font-size: 96px;
  top: 41%;
  width: 60%;
  z-index: 10;
  font-family: "Paytone One";
  box-shadow: 0px 0px 30px 5px rgba(0, 0, 0, 0.25);
  background: var(--colorClair);
}

.button {
  font-size: 96px;
  font-family: "Paytone One";
  cursor: pointer;
  color: #f1fff3;
  background: var(--colorClair);
  border: none;
  width: 100%;
  height: 50%;
  box-shadow: inset 0px 0px 30px 10px rgba(0, 0, 0, 0.25);
  transition: 0.3s ease-in;
}

.button:hover {
  background: var(--colorFonce);
}

.container_reponse {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
  width: 100%;
  box-shadow: inset 0px 0px 30px 10px rgba(0, 0, 0, 0.25);
}

.reponse {
  color: #f1fff3;
  font-family: "Paytone One";
  font-size: 144px;
}

.description {
  color: #f1fff3;
  text-align: center;
  font-size: 36px;
  margin-bottom: 36px;
}

.buttonNext {
  font-size: 48px;
  padding: 35px 114px;
  font-family: "Montserrat";
  color: #f1fff3;
  background-color: var(--colorFonce);
  border: none;
  cursor: pointer;
}

.container_footer {
  background-color: var(--colorClair);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1 1;
}

.question {
  font-size: 48px;
  width: 100%;
  text-align: center;
  color: #f1fff3;
  background: var(--colorFonce);
}

.container {
  background-color: var(--colorClair);
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f1fff3;
}

.header_titlee {
  margin-top: 156px;
  font-size: 72px;
  font-weight: bold;
}

.span {
  margin: 14px 0 35px 0;
  width: 853px;
  height: 12px;
  left: 10%;
  bottom: -12px;
  background: linear-gradient(to right, #006f66, #2fb9ae);
}

.header_miniscore {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 5em;
}

.header_subtitle {
  padding: 0.2em 0;
  color: #f1fff3;
  background-color: var(--colorFonce);
  text-align: center;
  width: 100%;
  font-size: 64px;
  font-weight: bold;
}

.footer {
  background-color: var(--colorClair);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.allContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.allImg_container {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "a . b" ". c ." "d . e";
}

.text_img_container {
  display: flex;
  justify-items: center;
  align-items: center;
  flex-direction: column;
}

.text {
  font-size: 30px;
  color: #f1fff3;
}

.img_container {
  position: relative;
  margin: 0 auto;
  display: flex;
  justify-items: center;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;

  background-color: #f1fff3;
  border-radius: 50%;
  padding: 1.3em;
}

.img_a {
  grid-area: a;
  transition: 0.3s ease-in-out 0s;
}
.img_b {
  grid-area: b;
  transition: 0.3s ease-in-out 0.3s;
}
.img_c {
  grid-area: c;
  cursor: pointer;
}
.img_d {
  grid-area: d;
  transition: 0.3s ease-in-out 0.9s;
}
.img_e {
  grid-area: e;
  transition: 0.3s ease-in-out 0.6s;
}

.animOff {
  opacity: 0;
}

.animOn {
  opacity: 1;
}

.padding3 {
  padding: 3em;
}
.nb_containerAll {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.nb_container2 {
  margin: 5em 0;
}

.allImg_container2 {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas:
    "a . b"
    "c d e"
    "f . g";
}

.pourcentage_pays_container {
  display: flex;
  justify-items: center;
  align-items: center;
  flex-direction: column;
  margin: 10em 0;
}

.pourcentage_container {
  background-color: #f1fff3;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 146px;
  height: 146px;
}

.pourcentage {
  font-size: 34px;
  font-weight: bold;
}

.img_a2 {
  grid-area: a;
  transition: 0.3s ease-in-out 0s;
  align-items: flex-end;
}
.img_b2 {
  grid-area: b;
  transition: 0.2s ease-in-out 0.2s;
  align-items: flex-start;
}
.img_c2 {
  grid-area: c;
  transition: 0.2s ease-in-out 0.4s;
  align-items: flex-start;
}
.img_d2 {
  grid-area: d;
  cursor: pointer;
}
.img_e2 {
  grid-area: e;
  transition: 0.2s ease-in-out 0.6s;
  align-items: flex-end;
}

.img_f2 {
  grid-area: f;
  transition: 0.2s ease-in-out 0.8s;
  align-items: flex-end;
}
.img_g2 {
  grid-area: g;
  transition: 0.2s ease-in-out 1s;
  align-items: flex-start;
}

.animOff2 {
  opacity: 0;
}

.animOn2 {
  opacity: 1;
}

.application {
  height: 100vh;
  display: flex;
  flex-direction: column;
}



