.container {
  background-color: var(--colorClair);
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f1fff3;
}

.header_titlee {
  margin-top: 156px;
  font-size: 72px;
  font-weight: bold;
}

.span {
  margin: 14px 0 35px 0;
  width: 853px;
  height: 12px;
  left: 10%;
  bottom: -12px;
  background: linear-gradient(to right, #006f66, #2fb9ae);
}

.header_miniscore {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 5em;
}

.header_subtitle {
  padding: 0.2em 0;
  color: #f1fff3;
  background-color: var(--colorFonce);
  text-align: center;
  width: 100%;
  font-size: 64px;
  font-weight: bold;
}

.footer {
  background-color: var(--colorClair);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.allContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.allImg_container {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "a . b" ". c ." "d . e";
}

.text_img_container {
  display: flex;
  justify-items: center;
  align-items: center;
  flex-direction: column;
}

.text {
  font-size: 30px;
  color: #f1fff3;
}

.img_container {
  position: relative;
  margin: 0 auto;
  display: flex;
  justify-items: center;
  align-items: center;
  width: fit-content;
  height: fit-content;

  background-color: #f1fff3;
  border-radius: 50%;
  padding: 1.3em;
}

.img_a {
  grid-area: a;
  transition: 0.3s ease-in-out 0s;
}
.img_b {
  grid-area: b;
  transition: 0.3s ease-in-out 0.3s;
}
.img_c {
  grid-area: c;
  cursor: pointer;
}
.img_d {
  grid-area: d;
  transition: 0.3s ease-in-out 0.9s;
}
.img_e {
  grid-area: e;
  transition: 0.3s ease-in-out 0.6s;
}

.animOff {
  opacity: 0;
}

.animOn {
  opacity: 1;
}

.padding3 {
  padding: 3em;
}
.nb_containerAll {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.nb_container2 {
  margin: 5em 0;
}

.allImg_container2 {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas:
    "a . b"
    "c d e"
    "f . g";
}

.pourcentage_pays_container {
  display: flex;
  justify-items: center;
  align-items: center;
  flex-direction: column;
  margin: 10em 0;
}

.pourcentage_container {
  background-color: #f1fff3;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 146px;
  height: 146px;
}

.pourcentage {
  font-size: 34px;
  font-weight: bold;
}

.img_a2 {
  grid-area: a;
  transition: 0.3s ease-in-out 0s;
  align-items: flex-end;
}
.img_b2 {
  grid-area: b;
  transition: 0.2s ease-in-out 0.2s;
  align-items: flex-start;
}
.img_c2 {
  grid-area: c;
  transition: 0.2s ease-in-out 0.4s;
  align-items: flex-start;
}
.img_d2 {
  grid-area: d;
  cursor: pointer;
}
.img_e2 {
  grid-area: e;
  transition: 0.2s ease-in-out 0.6s;
  align-items: flex-end;
}

.img_f2 {
  grid-area: f;
  transition: 0.2s ease-in-out 0.8s;
  align-items: flex-end;
}
.img_g2 {
  grid-area: g;
  transition: 0.2s ease-in-out 1s;
  align-items: flex-start;
}

.animOff2 {
  opacity: 0;
}

.animOn2 {
  opacity: 1;
}
